@charset "UTF-8";
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;600;800&display=swap');

/*Generic CSS*/

*,
*::before,
*::after {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

:root {
    --font-main:  'Inter', sans-serif;

    --fs-xl: 2rem;
    --fs-600: 1.5rem;
    --fs-500: 1.25rem;
    --fs-400: 1rem;
    --fs-200: .6rem;

    --fw-regular: 400;
    --fw-bold: 600;
    --fw-black: 800;
}

body,
input,
textarea,
button,
h1,
h2,
h3,
h4,
h5,
h6 {
    font-family: var(--font-main);
    font-size: var(--fs-400);
    font-weight: var(--fw-regular);
    color: #161616;
}

button, input {
    appearance: none;
    border: none;
    background: none;
    cursor: pointer;
    outline: none;
}

ul,
ol {
    list-style: none;
}

a {
    text-decoration: none;
    color: inherit;
}

img {
    max-width: 100%;
}

body {
    background-color: #edfeff;
}

#root {
    height: 100%;
    display: flex;
    flex-direction: column;
    gap: 2rem;
    align-items: center;
    justify-content: center;
}

main {
    display: grid;
    grid-template-columns: 1fr 50% 1fr;
    gap: clamp(1rem, 4vw, 2rem);
    align-items: center;
    justify-content: center;
    max-width: 720px;
    width: 90%;
}

.table {
    display: grid;
    width: 100%;
    grid-template-columns: repeat(4, 1fr);
    gap: clamp(.2rem, 1.5vw, .75rem);
}

.square {
    position: relative;
    aspect-ratio: 1/1;
    width: 100%;
    border-radius: 5px;
    background-color: #a2c9ff;
}

.square:has(.piece:not(.placed)) {
    cursor: pointer;
}

.side-table {
    display: grid;
    width: 100%;
    grid-template-columns: repeat(2, 1fr);
    gap: clamp(.2rem, 1.5vw, .75rem);
}

.side-square {
    aspect-ratio: 1/1;
    position: relative;
    width: 100%;
    border-radius: 5px;
    background-color: #4e709f; 
}

