footer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 1rem;
}

.stage {
    text-transform: uppercase;
    font-size: var(--fs-600);
    font-weight: var(--fw-bold);
}

.progress {
    display: flex;
    flex-direction: row;
    gap: .25rem;
}

.bar {
    position: relative;
    display: inline-block;
    border-radius: 20px;
    width: 200px;
    height: 20px;
    background: #a2c9ff;
    overflow: hidden;
}

.bar-fill {
    height: 100%;
    background-color: #4e709f;
}

.side-square:has(.piece):hover { cursor: pointer }
.side-square:hover > .piece { opacity: .5 }

/* Classifications */

.piece {
    aspect-ratio: 1/1;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

/* .flat {} */
.pierced::after { 
    aspect-ratio: 1/1;
    position: absolute;
    content: '';
    width: 50%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    border-radius: 5px;
}
.side-square .pierced::after { background-color: #4e709f; }
.square .pierced::after { background-color: #a2c9ff; }
.pierced.circle::after { border-radius: 50%;}

.dark { background-color: #161616; }
.light { background-color: #edfeff; }

.circle { border-radius: 50%; }
/* .squared {} */

.small { width: 60%; }
.large { width: 80%; }